import React from "react"
import PropTypes from "prop-types"
import Img from "gatsby-image"
import './thumbnail.css'
import styled from 'styled-components'
const GalleryItem = styled.div`
		margin-bottom: 30px;
		position: relative;
    cursor:pointer;
`
const GalleryOverlay = styled.div`
    background-color: rgba(255, 90, 95, .8);
		border-radius: 5px;
		bottom: 0;
		left: 0;
		position: absolute;
		right: 0;
		top: 0;
		width: 100%;
		-webkit-transform: scale(0);
		-moz-transform: scale(0);
		-o-transform: scale(0);
		-ms-transform: scale(0);
		transform: scale(0);
		-webkit-transition: all 0.3s ease 0s;
		-moz-transition: all 0.3s ease 0s;
		-o-transition: all 0.3s ease 0s;
		transition: all 0.3s ease 0s;
    z-index:998;
`
const Icon = styled.div `
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: 35%;
    text-align: center;
    color: #ffffff;
		font-size: 50px;
`
const ThumbGrid = ({ images, handleOpen, classes }) => {
  return images.map((image, i) => (
      <div className="col-sm-6 col-md-6 col-lg-4" key={i}>
		    <GalleryItem onClick={handleOpen(i)} key={i} className="gallery_item">
        <Img
          fluid={image.node.childImageSharp.fluid}  
          className="img-fluid img-circle-rounded w100"      
        />
        <GalleryOverlay key={i} className="gallery_overlay">
          <Icon>
            <span className="flaticon-zoom-in"></span>
          </Icon>
        </GalleryOverlay>
        </GalleryItem>
      </div>
      
  ))
}

ThumbGrid.propTypes = {
  classes: PropTypes.object,
  images: PropTypes.array,
}
export default (ThumbGrid)